/**
 * wordpress dependencies
 */
import { useBlockProps, InnerBlocks } from '@wordpress/block-editor';

function Save( props ) {
	const { attributes, setAttributes } = props;
	const blockProps = useBlockProps.save( {
		className: 'gutenberghub-tab-content',
	} );
	return (
		<div { ...blockProps }>
			<InnerBlocks.Content />
		</div>
	);
}
export default Save;
