/**
 * wordpress dependencies
 */
import { isEmpty } from 'lodash';
import { useEffect } from '@wordpress/element';
import { useSelect, useDispatch } from '@wordpress/data';
import { useBlockProps, InnerBlocks } from '@wordpress/block-editor';
/**
 * internal imports
 */
import { getParentBlock } from '../../utils';

function Edit( props ) {
	const { attributes, setAttributes, clientId, isSelected } = props;
	const blockProps = useBlockProps( {
		className: 'gutenberghub-tab-content',
	} );
	const blockIndex = useSelect( ( select ) => {
		const index = select( 'core/block-editor' ).getBlockIndex( clientId );

		return index;
	} );

	const template = [ [ 'core/paragraph' ] ];
	const { updateBlockAttributes } = useDispatch( 'core/block-editor' );

	const selectedBlockClientId = useSelect( ( select ) =>
		select( 'core/block-editor' ).getSelectedBlockClientId()
	);
	const contentBlock = getParentBlock(
		selectedBlockClientId,
		'ghub/tab-content'
	);

	const selectedBlockIndex = useSelect( ( select ) => {
		const index = select( 'core/block-editor' ).getBlockIndex(
			contentBlock?.clientId
		);

		return index;
	} );

	useEffect( () => {
		if ( isSelected || ! isEmpty( contentBlock ) ) {
			const parentBlock = getParentBlock(
				contentBlock?.clientId ?? clientId,
				'ghub/tabs-container'
			);
			if ( parentBlock ) {
				updateBlockAttributes( parentBlock.clientId, {
					activeTabIndex: selectedBlockIndex,
				} );
			}
		}
	}, [ isSelected, contentBlock ] );

	return (
		blockIndex === props.context.activeTabIndex && (
			<div { ...blockProps }>
				<InnerBlocks template={ template } />
			</div>
		)
	);
}
export default Edit;
